import '@testing-library/jest-dom';
import React from 'react';
import { render, screen, fireEvent } from '@testing-library/react';
import { Provider } from 'react-redux';
import { store } from '../src/renderer/store';
import DashboardLayout from '../src/renderer/features/Dashboard/components/DashboardLayout';

// Mock the panel components
jest.mock('../src/renderer/features/Dashboard/components/LeftPanel', () => {
  return function MockLeftPanel() {
    return <div data-testid="left-panel">Left Panel</div>;
  };
});

jest.mock('../src/renderer/features/Dashboard/components/CenterPanel', () => {
  return function MockCenterPanel() {
    return <div data-testid="center-panel">Center Panel</div>;
  };
});

jest.mock('../src/renderer/features/Dashboard/components/RightPanel', () => {
  return function MockRightPanel() {
    return <div data-testid="right-panel">Right Panel</div>;
  };
});

describe('DashboardLayout', () => {
  const renderWithProvider = (component: React.ReactElement) => {
    return render(
      <Provider store={store}>
        {component}
      </Provider>
    );
  };

  it('3분할 레이아웃이 정상적으로 렌더링된다', () => {
    renderWithProvider(<DashboardLayout />);
    
    expect(screen.getByTestId('left-panel')).toBeInTheDocument();
    expect(screen.getByTestId('center-panel')).toBeInTheDocument();
    expect(screen.getByTestId('right-panel')).toBeInTheDocument();
  });

  it('좌측 패널 토글 버튼이 존재한다', () => {
    renderWithProvider(<DashboardLayout />);
    
    const leftToggleButton = screen.getByRole('button', { name: /←/ });
    expect(leftToggleButton).toBeInTheDocument();
  });

  it('우측 패널 토글 버튼이 존재한다', () => {
    renderWithProvider(<DashboardLayout />);
    
    const rightToggleButton = screen.getByRole('button', { name: /→/ });
    expect(rightToggleButton).toBeInTheDocument();
  });

  it('좌측 패널 토글 버튼 클릭 시 상태가 변경된다', () => {
    renderWithProvider(<DashboardLayout />);
    
    const leftToggleButton = screen.getByRole('button', { name: /←/ });
    fireEvent.click(leftToggleButton);
    
    // 버튼 텍스트가 변경되는지 확인
    expect(screen.getByRole('button', { name: /→/ })).toBeInTheDocument();
  });

  it('우측 패널 토글 버튼 클릭 시 상태가 변경된다', () => {
    renderWithProvider(<DashboardLayout />);
    
    const rightToggleButton = screen.getByRole('button', { name: /→/ });
    fireEvent.click(rightToggleButton);
    
    // 버튼 텍스트가 변경되는지 확인
    expect(screen.getByRole('button', { name: /←/ })).toBeInTheDocument();
  });

  it('레이아웃이 100vh 높이를 가진다', () => {
    renderWithProvider(<DashboardLayout />);
    
    const layoutContainer = screen.getByTestId('left-panel').parentElement?.parentElement;
    expect(layoutContainer).toHaveStyle({ height: '100vh' });
  });

  it('접근성 속성이 포함되어 있다', () => {
    renderWithProvider(<DashboardLayout />);
    
    const toggleButtons = screen.getAllByRole('button');
    toggleButtons.forEach(button => {
      expect(button).toBeInTheDocument();
    });
  });
}); 